<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "constructionAreaComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      form: {
        area: [],
      },
      imgs: [],
      rules: {},
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "apply_Pk", label: "施工單號", width: "130" },
        { id: "supplier_Short_Name", label: "承攬商名稱", width: "130" },
        { id: "floor", label: "主區域", width: "130" },
        { id: "area_Name", label: "次區域", width: "130" },
        { id: "work_Type", label: "工種", width: "150" },
        { id: "work_Content", label: "施工內容", width: "130" },
        { id: "reg_Num", label: "施工人數", width: "130" },
      ],
      data: {},
      options: [],
      mainAreaDetail: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {
    locationImg() {
      return this.$store.state.locationImg;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    // tag點擊事件
    async tagHandleClick(evt, item) {
      const objData = {
        Work_Type_Pk: [item.work_Type_Pk],
        FloorId: [item.floor_Id],
        Location_Id: this.data.Location_Id,
        NorthOrSouth: this.data.NorthOrSouth,
      };
      const data = await this.$store.dispatch(
        "constructionMgmQryMainCheckInWorkType",
        objData
      );
      data.forEach((e, index) => {
        e.index = index + 1;
        e.work_Type = `${e.work_Type_Pk}${e.work_Type_Name}`;
      });
      this.tableData = JSON.parse(JSON.stringify(data));
      this.tableList = this.tableData.slice(0, 10);

      // 點選tag的active樣式
      const tags = document.querySelectorAll(".el-tag");
      tags.forEach((e) => e.classList.remove("active"));
      evt.target.classList.add("active");
    },
    // API取得資料
    async searchDetail() {
      const data = {
        FloorId: this.form.area,
        Location_Id: this.data.Location_Id,
        NorthOrSouth: this.data.NorthOrSouth,
      };
      const res = await this.$store.dispatch(
        "constructionMgmQryMainCheckInCountDetail",
        data
      );
      this.mainAreaDetail = res.data.data;
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.data = JSON.parse(this.$route.params.params);
    this.$route.meta.breadcrumb[1].name = this.data.description;
    const params = this.getURLNewParams(this.data);
    const res = await this.$store.dispatch(
      "constructionMgmQryMainCheckInCount",
      params
    );
    this.options = res.data.data;
  },
};
</script>

<template src="./constructionArea.html"></template>

<style lang="sass" scoped>
.el-tag
  cursor: pointer

  &.active
    background-color: #006bb2
    border-color: #006bb2
    color: #ffffff
</style>
